import React, { FC } from 'react'
import { graphql, Link } from 'gatsby'
import Layout from 'components/Layout'
import { Title, Text, Grid } from 'components/theme'
import Map from 'components/Map'
import { Container, Divider } from 'styles/globalStyle'
import { CentersPageTypes } from 'types/centersTypes'

type CentersPageParams = {
  data: CentersPageTypes
}

const CentersPage: FC<CentersPageParams> = ({ data }) => {
  const { page, centers } = data
  const markers = [
    {
      lat: 48.80767,
      lng: 2.2403,
    },
    {
      lat: 48.17289,
      lng: 2.25356,
    },
  ]

  return (
    <Layout onlyFooter>
      <Container textAlign="center" py="4rem">
        <Text color="primary.700" fontWeight="600" mb="0.75rem">
          {page.data.subheading.text}
        </Text>
        <Title>{page.data.title.text}</Title>
      </Container>
      <Map height="513px" markers={markers} />
      <Container py="4rem">
        <Text color="gray.100" fontWeight="600">
          Tous nos centres
        </Text>
        <Divider mt="0.75rem" mb="2.5rem" />
        <Grid columns={['1fr', '1fr 1fr', '', 'repeat(4, 1fr)']} spacing="2rem">
          {centers.nodes.map((center, idx) => (
            <Link key={idx} to={center.url}>
              <Text size="lg" color="gray.900" fontWeight="600" mb="0.5rem">
                {center.data.title.text}
              </Text>
              <Text mb="0.5rem">{center.data.address.text}</Text>
              <Text>TÃ©l : {center.data.phone_number.text}</Text>
              <Text>Email : {center.data.email.text}</Text>
            </Link>
          ))}
        </Grid>
      </Container>
    </Layout>
  )
}

export const query = graphql`
  query {
    page: prismicListCentersPage {
      data {
        title {
          text
        }
        subheading {
          text
        }
      }
    }
    centers: allPrismicCenters {
      nodes {
        data {
          title {
            text
          }
          phone_number {
            text
          }
          email {
            text
          }
          address {
            text
          }
        }
        url
      }
    }
  }
`

export default CentersPage
